* {
  margin:0;
  padding:0;
}
html {
  height:100%;
  width:100%;
}
body {
  height:100%;
  width:100%;
}
body {
  margin: 0;
  padding: 0;
  font-family: 'Inconsolata', monospace;
}

.copy {
  margin: 10px 0px;
  font-weight: normal;
  line-height: 26px;
  text-align: justify;
  text-justify: inter-word;
}

.copy span {
  font-weight: bold;
}
.container-info {
  display: inline-block;
  position: relative;
  width: 40%;
  min-height: 490px;
  background-image: url(/static/media/bg_01.ec89e53c.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

@media (min-width: 768px) {
  .container-info {
    float: left;
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: 40%;
  }
}

.principal{
  margin: 20px 0px 5px 0px;
  font-family: 'Inconsolata', monospace;
  text-align: center;
  color: #f7f7f7;
}
.principal h1 {
  font-size: 32px;
}

.principal h2{
  font-size: 16px;
}

.principal h3 {
  font-size: 12px;
}

.principal h4 {
  margin: 15px 0px;
  font-size: 16px;
}

.principal h4 span{
  color: #cb3903;
  font-weight: bold;
}

.principal p span{
  color: #cb3903;
  font-weight: bold;
}

.inner-block {
    padding: 1.25rem
}
@media screen and (max-width: 768px) {
    .inner-block {
        padding: 1.5625rem
    }
}
.inner-block .content-block{
  margin: 25% 0 0;
}

.avatar {
  display: block;
  margin: auto;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 5px solid #e4e4e4;;
  transition: 0.5s;
}
.avatar:hover{
  transition: 0.5s;
  border-radius: 30%;
  border-color: transparent;
}

.footer {
  padding-top: 50px;
}

.footer p {
  text-align: center;
  font-size: 12px;
  color: #edecec;
}

.footer p i {
  color: #ee2a07;
}

.footer ul {
  margin: 0;
  padding: 5px;
  list-style-type: none;
  text-align: center;
}

.footer ul li {
  display: inline;
}

.footer ul li a {
  text-decoration: none;
  cursor: pointer;
  padding: 5px 10px;
  color: #fff;
  font-size: 16px;
  transition: 0.3s;
}

.footer ul li a:hover {
  transition: 0.3s;
  color: #cdcdcd;
}

.container-main {
  display: inline-block;
  background: #ffffff;
  width: calc(60%);
}
@media (min-width: 768px) {
  .container-main {
    position: fixed;
    top: 0;
    right: 0;
    height: 100%;
    width: calc(60%);
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    overflow-x: hidden;
  }
}

.about{
  margin: 75px;
  overflow: auto;
  font-family: 'Inconsolata', monospace;
}

.about h1{
  font-size: 36px;
  margin: 10px 0px;
  color: #231e24;
}

.timeline{
  margin: 30px 0px;
}

.timeline h2{
  font-size: 18px;
  margin: 20px 0px;
  color: #231e24;
  text-align: center;
}

.timeline img{
  display: block;
  width: 500px;
  margin: 20px auto;
}

.works {
  margin-top: 50px;
}
.works h2{
  margin: 50px 0px 5px !important;
  font-size: 20px;
  font-weight: bold;
  margin: 10px 0px;
  color: #231e24;
}

.works .list {
    font-size: 14px;
    margin: 10px 0px;
    padding: 10px;
    font-weight: normal;
    line-height: 26px;
    text-align: justify;
    text-justify: inter-word;
}

.works .list-2 {
  font-size: 14px;
  margin: 10px 0px;
  padding: 10px;
  font-weight: normal;
  line-height: 26px;
  text-align: justify;
  text-justify: inter-word;
  background: #FFF;
  border-radius: 5px;
  border: 1px solid rgba(0,0,0,.125);
}

.works .list li{
  list-style: none;
}
.works .list li:first-child {
    font-weight: bold;
    font-size: 16px;
}

.works .list li:nth-child(2) {
  font-weight: bold
}

.works .list-2 li{
  list-style: none;
}
.works .list-2 li:nth-child(2) {
    font-weight: bold;
    font-size: 16px;
}

.works .list-2 li:nth-child(3) {
  font-weight: bold
}

.img-work{
  border: 1px solid rgba(0,0,0,.125);
  border-radius: 10px;
}
.container-menu {
  display: inline-block;
  position: relative;
  width: 100%;
  min-height: 490px;
  background: #181818;
}

@media (min-width: 768px) {
  .container-menu {
    position: fixed;
    left: calc(40%); 
    top: 0;
    height: 100%;
    width: 50px;
  }
}



