.container-main {
  display: inline-block;
  background: #ffffff;
  width: calc(60%);
}
@media (min-width: 768px) {
  .container-main {
    position: fixed;
    top: 0;
    right: 0;
    height: 100%;
    width: calc(60%);
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    overflow-x: hidden;
  }
}

.about{
  margin: 75px;
  overflow: auto;
  font-family: 'Inconsolata', monospace;
}

.about h1{
  font-size: 36px;
  margin: 10px 0px;
  color: #231e24;
}

.timeline{
  margin: 30px 0px;
}

.timeline h2{
  font-size: 18px;
  margin: 20px 0px;
  color: #231e24;
  text-align: center;
}

.timeline img{
  display: block;
  width: 500px;
  margin: 20px auto;
}

.works {
  margin-top: 50px;
}
.works h2{
  margin: 50px 0px 5px !important;
  font-size: 20px;
  font-weight: bold;
  margin: 10px 0px;
  color: #231e24;
}

.works .list {
    font-size: 14px;
    margin: 10px 0px;
    padding: 10px;
    font-weight: normal;
    line-height: 26px;
    text-align: justify;
    text-justify: inter-word;
}

.works .list-2 {
  font-size: 14px;
  margin: 10px 0px;
  padding: 10px;
  font-weight: normal;
  line-height: 26px;
  text-align: justify;
  text-justify: inter-word;
  background: #FFF;
  border-radius: 5px;
  border: 1px solid rgba(0,0,0,.125);
}

.works .list li{
  list-style: none;
}
.works .list li:first-child {
    font-weight: bold;
    font-size: 16px;
}

.works .list li:nth-child(2) {
  font-weight: bold
}

.works .list-2 li{
  list-style: none;
}
.works .list-2 li:nth-child(2) {
    font-weight: bold;
    font-size: 16px;
}

.works .list-2 li:nth-child(3) {
  font-weight: bold
}

.img-work{
  border: 1px solid rgba(0,0,0,.125);
  border-radius: 10px;
}